.enter {
  max-width: 500px;
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 100px;
  & input {
    &::placeholder {
      color: #9e9e9e;
    }
  }
  &__title {
    font-size: 48px;
    line-height: 100%;
    color: #333333;
    text-align: center;
    & span {
      color: #FF4D1C;
    }

    @include width-1200 {
      font-size: 38px;
      max-width: 400px;
      margin: 0 auto;
    }
    @include width-768 {
      font-size: 28px;
      max-width: 300px;
    }
  }
  & .pp__check {
    & label {
      & a {
        color: #EC1744;
        text-decoration: underline;
        transition: all 0.4s;
        &:hover {
          color: #333333;
        }
      }
    }
  }
}
